<template>
  <b-modal v-model="show" @close="closeTaskViewModal()">
    <section>
      <div class="is-cwhite-1 message  notification padding-075">
        <view-one-pro-task :task.sync="data" />
      </div>
    </section>
  </b-modal>
</template>

<script>
import ViewOneProTask from '@/components/Article/ViewOneProTask'

export default {
  /**
   * 展示任务详情的modal
   */
  name: 'ViewTaskModal',
  components: {
    ViewOneProTask
  },
  data() {
    return {
      /**
       * 显示modal
       */
      show: false,
      data: {}
    }
  },
  methods: {
    /**
     * 打开,赋值
     */
    open(data) {
      this.show = true
      // 赋值
      this.data = data
    },
    /**
     * 关闭任务后,应该触发
     */
    closeTaskViewModal() {
      // update 2021-1-19-> 选择关闭closemodal后，此时的data数据是更新完毕后的
      this.$emit('updateTaskData', this.data)
    }
  }
}
</script>

<style scoped>
.padding-075 {
  padding: 0.75em 0.75em;
}
</style>
