<template>
  <!--创建新的任务-->
  <div>
    <b-modal v-model="show">
      <div class="is-cgray-2  message padding-0">
        <b-steps :rounded="false" :has-navigation="false" class="padding-05" @input="changeActiveStep($event)">
          <b-step-item step="1" value="1" label="基本信息" clickable type="is-cgreen-3">
            <div>
              <b-field>
                <b-field>
                  <strong class="mr-5 pt-2">任务名:</strong>
                  <b-input v-model="newtask.taskname" icon="textbox" class="mr-5"></b-input>
                </b-field>
                <b-field>
                  <strong class="mr-5 pt-2">指派给:</strong>
                  <b-field class="mr-5">
                    <b-select v-model="newtask.assigner" placeholder="选择人员" :expanded="true">
                      <option v-for="(item, index) in allusers" :key="index" :value="item">{{ item }}</option>
                    </b-select>
                  </b-field>
                </b-field>
                <b-field>
                  <strong class="mr-5 pt-2">任务类型:</strong>
                  <b-field class="mr-5">
                    <b-select v-model="newtask.tasktype" placeholder="任务类型" :expanded="true">
                      <option v-for="(item, index) in alltasktype" :key="index" :value="item">{{ item }}</option>
                    </b-select>
                  </b-field>
                </b-field>
              </b-field>
              <b-field>
                <p class="mr-5 pt-2">
                  <strong class="mr-2">ID:</strong>
                  {{ newtask.taskid }}
                </p>
                <p class="mr-5 pt-2">
                  <strong class="mr-2">任务状态:</strong>
                  <b-tag type="is-success">Active</b-tag>
                </p>
              </b-field>
              <b-field>
                <b-field>
                  <p class="mr-5 pt-2">
                    <strong class="mr-2">创建时间:</strong>
                    {{ newtask.createdate }}
                  </p>
                </b-field>
                <p class="mr-5 pt-2">
                  <strong class="mr-2">修改者:</strong>
                  {{ newtask.modifyautor }}
                </p>
                <b-field>
                  <p class="mr-5 pt-2">
                    <strong class="mr-2">修改时间:</strong>
                    {{ newtask.modifydate }}
                  </p>
                </b-field>
              </b-field>
            </div>
          </b-step-item>
          <b-step-item step="2" value="2" label="更多信息" clickable type="is-cgreen-3">
            <div>
              <div class="columns">
                <div class="column">
                  <b-field message="如需要, 请确认配置文件格式正确">
                    <b-field class="file">
                      <strong class="mr-5 pt-2">配置文件:</strong>
                      <b-upload v-model="configlist" accept=".json,.config" class="mr-5" :multiple="true">
                        <a class="button is-cwhite-1">
                          <b-icon icon="upload" />
                          <span>上传配置文件</span>
                        </a>
                      </b-upload>
                    </b-field>
                  </b-field>
                  <b-field message="如需要, 请确认脚本文件格式正确">
                    <b-field class="file">
                      <strong class="mr-5 pt-2">脚本文件:</strong>
                      <b-upload v-model="scriptlist" accept=".ts,.js,.ros,.cs,.xml" class="mr-5" :multiple="true">
                        <a class="button is-cwhite-1">
                          <b-icon icon="upload" />
                          <span>上传脚本文件</span>
                        </a>
                      </b-upload>
                    </b-field>
                  </b-field>
                  <b-field label="设备筛选器:" :type="verifydevce">
                    <b-input
                      v-model="devicetext.input"
                      placeholder="输入设备IP..."
                      @keyup.enter.native="enterVerifyIP(devicetext.output)"
                      @input="deviceChanged($event)"
                    ></b-input>
                  </b-field>
                </div>
                <div class="column">
                  <div v-for="(pitem, pindex) in [configlist, scriptlist, newtask.devicelist]" :key="pindex" class="tags" type="is-cgray-2">
                    <b-tag v-for="(citem, cindex) in pitem" :key="cindex" type="is-cgray-2">
                      <span v-if="typeof citem === 'object'">{{ citem.name }}</span>
                      <span v-else>{{ citem }}</span>
                      <button class="delete is-small" type="button" @click="deleteFile(index, pitem)"></button>
                    </b-tag>
                  </div>
                </div>
              </div>
            </div>
          </b-step-item>
          <b-step-item step="3" value="3" label="任务确认" clickable type="is-cgreen-3">
            <view-one-pro-task :task.sync="newtask" />
            <div class="level">
              <div class="level level-left" />
              <div class="level level-right">
                <b-button class="level-item" type="is-cgreen-3" size="is-small" @click="close()">保存</b-button>
              </div>
            </div>
          </b-step-item>
        </b-steps>
      </div>
    </b-modal>
  </div>
</template>

<script>
import dateFormat from 'dateformat'
import ViewOneProTask from '@/components/Article/ViewOneProTask'
export default {
  name: 'CreateNewTaskModal',
  components: {
    ViewOneProTask
  },

  data() {
    return {
      /**
       * 是否显示
       */
      show: false,

      /**
       * 设备输入框
       */
      devicetext: {
        input: '',
        output: ''
      },
      /**
       * 脚本列表(全)
       */
      scriptlist: [],
      /**
       * 配置列表(全)
       */
      configlist: [],

      /**
       * 与b-field的type绑定
       * 红色,设备验证不通过
       * 绿色,设备验证通过
       */
      verifydevce: '',
      /**
       * 新任务
       * 父组件调用
       */
      newtask: {
        createdate: this.createDate(),
        modifydate: this.createDate(),
        assigner: '',
        createautor: '',
        modifyautor: '未命名',
        counter: 0,
        usefilename: '',
        case: [],
        us: [],
        devicelist: [],
        scriptlist: [],
        configlist: [],
        reviewautor: '未命名',
        reviewstatus: 'Not Start',
        taskid: this.createGUID(),
        taskname: '',
        tasktype: '',
        taskstatus: 'Active',
        /**
         * update 2020-12-25 是否为离线模式
         */
        isoffline: true
      },

      allusers: ['居溱', '刘凯', '冯涛', '李荧辉'],
      alltasktype: ['develop', 'debug', 'review', 'running', 'release'],
      alldevice: ['172.30.19.15(scale)', '172.30.19.17', '172.30.19.11', '172.30.19.12', '127.0.0.5', '127.0.0.6', '127.0.0.7']
    }
  },
  mounted() {
    this.getUser()
  },
  methods: {
    /**
     * 打开,赋值
     */
    open() {
      this.show = true
    },

    /**
     * 关闭article并顺便更新newtask
     */
    close() {
      // console.log(this.newtask)
      this.$emit('returnNewTask', this.newtask)
      // this.$parent.close()
      this.show = false
    },

    /**
     * 获取用户
     * TODO: 还没做,等着做
     */
    getUser() {
      const nickname = localStorage.getItem('ro_nickname')
      // console.log('昵称:' + nickname)
      this.newtask.createautor = nickname + '_test'
    },

    /**
     * 生成guid
     */
    createGUID() {
      let guid = ''
      for (let i = 1; i <= 32; i++) {
        const n = Math.floor(Math.random() * 16.0).toString(16)
        guid += n
        if (i === 8 || i === 12 || i === 16 || i === 20) {
          guid += '-'
        }
      }
      return guid
    },
    /**
     * 生成日期
     */
    createDate() {
      const now = new Date()
      const date = dateFormat(now, 'isoUtcDateTime')
      return date
    },
    /**
     * 删除文件列表的某一个
     */
    deleteFile(index, array) {
      array.splice(index, 1)
      console.log(this.configlist)
    },

    /**
     * 设备text被改变时, 验证色彩也在改变
     */
    deviceChanged(e) {
      for (const item of this.alldevice) {
        if (e === item) {
          this.verifydevce = 'is-cgreen-3'
          this.devicetext.output = item
          break
        }
        this.devicetext.output = ''
        this.verifydevce = 'is-cred-1'
      }
    },

    /**
     * 键盘回车键，验证IP是否有效
     */
    enterVerifyIP(text) {
      if (text !== '') {
        this.newtask.devicelist.push(text)
      }
    },

    /**
     * 改变step活动窗口
     */
    changeActiveStep(e) {
      /**
       * 到达确认任务界面
       */
      if (e === '3') {
        if (this.configlist.length > 0) {
          this.newtask.configlist = [] // 初始化
          this.configlist.forEach(element => {
            this.newtask.configlist.push(element.name)
          })
        }
        if (this.scriptlist.length > 0) {
          this.newtask.scriptlist = [] // 初始化
          this.scriptlist.forEach(element => {
            this.newtask.scriptlist.push(element.name)
          })
        }
      }
    }
  }
}
</script>

<style scoped>
.padding-0 {
  padding: 0rem;
}
.padding-05 {
  padding: 0.5rem;
}
</style>
