<template>
  <div>
    <b-table :data="localdata" :per-page="prepage" pagination-simple paginated focusable :selected.sync="selectdata" @dblclick="openTaskDetail()">
      <template v-for="column in columns">
        <b-table-column :key="column.id" v-bind="column">
          <template v-if="column.searchable" #searchable="props">
            <b-input v-model="props.filters[props.column.field]" placeholder="检索" icon="magnify" size="is-small" />
          </template>
          <template v-slot="props">
            <div v-if="column.field === 'tasktype'">
              <b-tag type="is-cblue-2" size="is-meduim">{{ props.row[column.field] }}</b-tag>
            </div>
            <div v-else>
              {{ props.row[column.field] }}
            </div>
          </template>
        </b-table-column>
      </template>
    </b-table>
    <view-task-modal ref="viewtask" @updateTaskData="updateTaskData" />
  </div>
</template>

<script>
/**
 * OBSOLETE: 2020-2-9 可能此方法会是一个过期的
 * 个人任务界面 一个单独任务类型的子table
 */
import ViewTaskModal from '@/components/Modal/ViewTaskModal'
export default {
  name: 'PersonalTaskTable',
  components: {
    ViewTaskModal
  },
  props: {
    /**
     * 每页默认显示
     */
    prepage: {
      type: Number,
      default: 5
    },
    /**
     * 所有数据,通过up方法更新
     */
    alldata: Array,
    width: {
      type: Number,
      default: 280
    },
    showtaskid: {
      type: Boolean,
      default: false
    },
    allowsearch: {
      type: Boolean,
      default: false
    }
  },
  data() {
    return {
      /**
       * 当前选用的任务
       */
      selectdata: {},

      /**
       *  供以数据监听使用，本地数据,将上层数据与本层数据隔离开,仅在本地做数据的处理
       */
      localdata: [],

      /**
       * title头
       */
      columns: [
        {
          field: 'id',
          label: 'ID',
          width: '40',
          numeric: true,
          centered: true,
          searchable: false
        },
        {
          field: 'taskid',
          label: '任务 id',
          width: this.width,
          visible: this.showtaskid,
          searchable: false
        },
        {
          field: 'taskname',
          label: '任务名称',
          width: this.width,
          searchable: true
        },
        {
          field: 'tasktype',
          label: '类型',
          width: '100',
          searchable: true
        },
        {
          field: 'taskstatus',
          label: '状态',
          width: '100',
          searchable: true
        }
      ]
    }
  },
  watch: {
    alldata: {
      /**
       * 监听props的绑定数据
       */
      // immediate: true,
      handler(val) {
        this.localdata = val
      }
    },
    /**
     * 监听已选择的数据
     */
    selectdata: {
      // immediate: true,
      handler(val) {
        /**
         * 选择项目更新也要更新到前端
         *  */
        this.$emit('changeSel', val)
        console.log('change')
      }
    }
  },
  mounted() {
    this.init()
  },
  methods: {
    /**
     * 初始化
     */
    init() {},

    /**
     * 编辑完毕的数据也要更新到父组件，或者没更新过
     */
    updateTaskData(data) {
      this.$emit('updateTaskData', data)
    },

    /**
     * 双击弹出详情
     */
    openTaskDetail() {
      // 作为一个modal弹出任务详情
      this.$refs.viewtask.open(this.selectdata)
    }
  }
}
</script>

<style scoped>
.margin-0 {
  margin: 0rem;
}
</style>
