<template>
  <!--个人任务列表界面-->
  <div>
    <title-bar :title-stack="titleStack" />
    <section class="section is-main-section">
      <b-tabs type="is-boxed" size="is-medium">
        <b-tab-item label="个人任务" icon="file-tree" :disabled="true">
          <section>
            <nav class="level margin-0">
              <div v-for="item in protaskcontent" :key="item.name" class="level-item has-text-centered">
                <div>
                  <p class="heading">{{ item.data.label }}</p>
                  <p class="title">{{ item.data.value }}</p>
                </div>
              </div>
            </nav>
            <b-progress type="is-cblue-3" :value="80" class="margin-0" />
            <hr class="margin-05" />
            <b-field grouped group-multiline class="buttons">
              <b-button type="is-cred-2" class="has-text-dark" size="is-small" icon-left="new-box" @click="createNewTask()">
                新建
              </b-button>
              <b-button class="has-text-dark" size="is-small" icon-left="delete" @click="deleteTask()">
                删除
              </b-button>
            </b-field>
            <hr class="margin-05 has-background-link" />
          </section>
          <section>
            <b-tabs v-model="activetab" type="is-boxed" size="is-small" class="margin-05" @input="changeTab">
              <b-tab-item v-for="(item, index) in protaskcontent" :key="index" :value="item.name" class="padding-05">
                <template slot="header">
                  <b-icon :icon="item.data.icon" />
                  <span>
                    {{ item.data.label }}
                  </span>
                  <b-tag>{{ item.data.value }}</b-tag>
                </template>
                <personal-task-table ref="ptt" :alldata="activetabtask" @updateTaskData="updateTaskData" @changeSel="changeSel" />
              </b-tab-item>
            </b-tabs>
          </section>
        </b-tab-item>
      </b-tabs>
    </section>
    <delete-modal ref="delmodal" @returnResult="returnResult" />
    <create-new-task-modal ref="createnewmodal" @returnNewTask="returnNewTask" />
  </div>
</template>

<script>
import TitleBar from '@/components/TitleBar'
import PersonalTaskTable from '@/components/Table/PersonalTaskTable'
import CreateNewTaskModal from '@/components/Modal/CreateNewTaskModal'
import DeleteModal from '@/components/Modal/DeleteModal'
import { nowdate } from '../../scripts/common'
export default {
  name: 'TaskList',
  components: {
    TitleBar,
    PersonalTaskTable,
    CreateNewTaskModal,
    DeleteModal
  },
  data() {
    return {
      /**
       * 总任务
       */
      totaltask: [],

      /**
       * 显示活动的任务
       */
      activetabtask: [],

      /**
       * 活动的table sheet
       */
      activetab: '',
      /**
       * 新建的modal的开关
       */
      showmodal: false,

      /**
       * 已选择的task
       */
      selectedtask: {},

      /**
       * 个人任务概述 tab字段表述
       */
      protaskcontent: []
    }
  },
  computed: {
    titleStack() {
      return ['RoWalock', '个人任务']
    }
  },
  mounted() {
    this.init()
  },
  methods: {
    /**
     * 默认初始化
     */
    init() {
      // 组装数据
      const status = ['Active', 'Done', 'Block']
      for (let index = 0; index < 50; index++) {
        const sar = Math.floor(Math.random() * status.length) // 随机状态
        const element = {
          id: index,
          createdate: nowdate(),
          modifydate: nowdate(),
          assigner: '居溱',
          createautor: '冯涛',
          modifyautor: '刘凯',
          counter: 44,
          case: ['TTP 1111', 'TTP 2222', 'TTP 3333', 'TTP 4444'],
          us: ['US-0001 测试', 'US-0002 测试', 'US-0003 测试', 'US-0004 测试'],
          devicelist: [],
          scriptlist: ['test1.ts', 'test2.js', 'test3.xml'],
          configlist: ['archonetestttol.json'],
          reviewautor: '李荧辉',
          reviewstatus: 'need review',
          taskid: '9c7ebdcc-0b3f-4f31-8579-1319ad9658cc',
          taskname: `Task0001_MTStar_US00` + index + `自动化测试`,
          tasktype: 'Develop',
          taskstatus: status[sar],
          isoffline: true
        }
        this.totaltask.push(element)
      }

      // 更新个人任务的tab字段头等
      this.protaskcontent = [
        {
          name: 'total',
          data: {
            value: '120',
            icon: 'counter',
            label: '总任务'
          }
        },
        {
          name: 'done',
          data: {
            value: '44',
            icon: 'comment-check',
            label: '已完成任务'
          }
        },
        {
          name: 'active',
          data: {
            value: '23',
            icon: 'chart-donut',
            label: '正在活动任务'
          }
        },
        {
          name: 'block',
          data: {
            value: '76',
            icon: 'ghost',
            label: '挂起任务'
          }
        }
      ]

      this.changeTab(this.activetab)
    },

    /**
     * 改变tab sheet
     */
    changeTab(value) {
      // console.log(value)
      // 设置活动的任务tab的为空
      this.activetabtask = []
      // 根据状态分拆到各个tab内，当tab进行更改时执行
      if (value === 'total') {
        this.activetabtask = this.totaltask
      } else {
        for (let index = 0; index < this.totaltask.length; index++) {
          const element = this.totaltask[index]
          if (element.taskstatus.toLowerCase() === value) {
            this.activetabtask.push(element)
          }
        }
      }
    },

    /**
     * 从子组件传递至上部组件的数据
     */
    updateTaskData(data) {
      /** 替换数据 */
      this.totaltask[data.id] = data
      // 再刷新table
      this.changeTab(this.activetab)
    },

    /**
     * 子组件新选择了task，更新
     */
    changeSel(data) {
      this.selectedtask = data
    },
    /**
     * 创建一个新的任务
     */
    createNewTask() {
      this.$refs.createnewmodal.open()
    },
    /**
     * 获取一个新任务数据
     */
    returnNewTask(data) {
      data.id = this.totaltask.length
      this.totaltask.push(data)
      // 再刷新table
      this.changeTab(this.activetab)
    },
    /**
     * 删除任务
     */
    deleteTask() {
      this.$refs.delmodal.open()
    },

    /**
     *获取删除标识
     */
    returnResult(value) {
      if (value === true) {
        // 验证的删除标识为true时,删除被选择的项目
        this.totaltask.splice(this.selectedtask.id, 1)
        // 再刷新table
        this.changeTab(this.activetab)
      }
    }
  }
}
</script>

<style scoped>
.margin-0 {
  margin: 0rem;
}

.margin-05 {
  margin: 0.5rem;
}

.padding-05 {
  padding: 0.5rem;
}
</style>
