<template>
  <b-modal v-model="show" :width="600">
    <section>
      <b-message title="确认提示" type="is-cyellow-1">
        <b-field label="验证密码" :message="['请输入正确的密码']">
          <b-input v-model="delpsd" type="password" maxlength="6" />
        </b-field>
        <b-button type="is-cred-2" @click="clickDeleBtn()">确认删除</b-button>
      </b-message>
    </section>
  </b-modal>
</template>

<script>
export default {
  name: 'DeleteModal',
  data() {
    return {
      /**
       * 删除密码-> 246813
       */
      delpsd: '',
      /**
       * 显示modal
       */
      show: false,
      verifyresult: false
    }
  },
  methods: {
    /**
     * 点击删除按钮
     */
    clickDeleBtn() {
      if (this.delpsd === '246813') {
        this.verifyresult = true
        this.$buefy.toast.open({
          duration: 3000,
          message: `对象` + `<b>已被删除</b>`,
          position: 'is-bottom',
          type: 'is-danger'
        })
      } else {
        this.verifyresult = false
        this.$buefy.toast.open({
          duration: 3000,
          message: `管理密码不正确,请确认重试`,
          type: 'is-warning'
        })
      }
      this.$emit('returnResult', this.verifyresult)
      this.delpsd = '' // 初始化
      this.show = false // 关闭
    },

    /**
     * 打开,赋值
     */
    open() {
      this.delpsd = '' // 初始化
      this.show = true
    }
  }
}
</script>

